.button {
  color: #fff;
  background: #e86f54;
  border: 0;
  border-radius: 38px;
  outline: none;
  font-weight: 500;
  line-height: 24px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.button:hover, .button:focus {
  box-shadow: 0 2px 4px 2px #d7d7d7;
}

.button:hover {
  cursor: pointer;
  background: #e25b41;
  transform: translateY(-5px);
}

.button:focus {
  background: #fa6f54;
  box-shadow: 0 4px 16px #fa6f5433;
}

.button--sm {
  width: 170px;
  height: 40px;
  padding: 14px 28px;
  font-size: 18px;
}

@media screen and (min-width: 768px) {
  .button--sm {
    width: 280px;
    height: 64px;
    padding: 16px 32px;
    font-size: 24px;
  }
}

.button--md {
  width: 208px;
  height: 44px;
  padding: 14px 28px;
  font-size: 20px;
}

@media screen and (min-width: 768px) {
  .button--md {
    width: 280px;
    height: 64px;
    padding: 16px 32px;
    font-size: 24px;
  }
}

