  line-height: 1.211;
}

.hero__textbox {
  width: 305px;
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 135px;
}

.hero__text {
  width: 46px;
  text-align: left;
  color: #000;
  justify-content: center;
  align-items: center;
  padding-left: 50px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.211;
  display: flex;
}

.hero__button {
  background: none;
  border: none;
  justify-content: center;
  display: flex;
}

.hero__btn {
  width: 64px;
  height: 76px;
  cursor: pointer;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.hero__btn:hover {
  animation: 10s linear .1s infinite alternate shopnow;
}

@keyframes shopnow {
  from {
    filter: invert(98%) sepia(89%) saturate(1281%) hue-rotate(292deg) brightness(107%) contrast(93%);
    transform: rotate(0)scale(1);
  }

  to {
    filter: invert(75%) sepia(42%) saturate(6520%) hue-rotate(325deg) brightness(95%) contrast(90%);
    transform: rotate(360deg)scale(1.2);
  }
}

