.backdrop {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: #0000004d;
  transition: opacity .5s, visibility .5s;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  transition: transform .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.is-hidden .modal {
  transform: translate(-50%, 100%);
}

.modal__close {
  cursor: pointer;
  background-color: #0000;
  border: 0;
  padding: 0;
  position: absolute;
  top: 14px;
  right: 14px;
}

.modal__close:hover {
  fill: #e25b41;
}

.modal__close:focus {
  fill: #fa6f54;
}

@media screen and (min-width: 768px) {
